<template>
	<view class="" style="flex-flow: column nowrap;width: 750upx; ">
		<navigationview :current="current" :values="items" v-on:clickItem="onClickItem" :list_id="list_id"></navigationview>

		<view class="" style="flex-flow:column;position:fixed;top:120upx;background-color: white;z-index: 30;	animation: mymove 0.3s linear ;"
		 v-if="mulu_flag=='paixu'">
			<scroll-view scroll-y style=" height: 270upx;width: 750upx;margin:10upx 30upx;">
				<view class="" style="width: 750upx;height: 40upx;line-height: 40upx; padding: 10upx;  font-size: 30upx;color: black;">字段</view>
				<view style="width: 750upx;flex-flow: row wrap;">
					<view :class="['rank_title_bac',rank['paixu'][index] ? 'bac_blue_white' : '']" v-for="(item,index) in  rank['rank_title']"
					 :key="index" @click="tap_paixu(index)">
						{{rank['rank_title'][index]}}
					</view>
				</view>
				<view class="" style="width: 750upx;height: 40upx;line-height: 40upx; padding: 10upx;  font-size: 30upx;color: black;">排序方式</view>
				<view style="width: 750upx;flex-flow: row wrap;">
					<view :class="['rank_title_bac',paixu_sort_flag=='bigtosmall' ? 'bac_red_white' : '']" @click="tap_paixu_sort('bigtosmall')">顺序</view>
					<view :class="['rank_title_bac',paixu_sort_flag=='smalltobig' ? 'bac_red_white' : '']" @click="tap_paixu_sort('smalltobig')">逆序</view>
				</view>
			</scroll-view>
			<view style="width: 750upx;flex-flow: row;font-size: 30upx;">
				<view style="width: 375upx;height:70upx;justify-content: center;align-items: center;background-color: #DCDCDC;"
				 @click="tap_mulu('paixu')">取消</view>
				<view style="width: 375upx;height:70upx;justify-content: center;align-items: center;background-color: #0000FF;color: white;"
				 @click="tap_paixu_queren('paixu')">确定</view>
			</view>
		</view>

		<view class="" style="flex-flow:column;position:fixed;top:120upx;background-color: white;z-index: 30;	animation: mymove 0.3s linear ;"
		 v-if="mulu_flag=='ziduan'">
			<scroll-view scroll-y style=" height: 270upx;width: 750upx;margin:10upx 30upx;">
				<view class="" style="width: 750upx;height: 40upx;line-height: 40upx; padding: 10upx;  font-size: 30upx;color: black;">字段</view>
				<view style="width: 750upx;flex-flow: row wrap;">
					<view :class="['rank_title_bac',rank['ziduan'][index] ? 'bac_blue_white' : '']" v-for="(item,index) in  rank['rank_title']"
					 :key="index" @click="tap_ziduan(index)">
						{{rank['rank_title'][index]}}
					</view>
				</view>
			</scroll-view>
			<view style="width: 750upx;flex-flow: row;font-size: 30upx;">
				<view style="width: 375upx;height:70upx;justify-content: center;align-items: center;background-color: #DCDCDC;"
				 @click="tap_mulu('ziduan')">取消</view>
				<view style="width: 375upx;height:70upx;justify-content: center;align-items: center;background-color: #0000FF;color: white;"
				 @click="tap_ziduan_queren('ziduan')">确定</view>
			</view>
		</view>

		<view class="" v-show="current === 0" style=" width: 650upx; height: 50upx; flex-flow: row nowrap; background-color:white;justify-content:space-between;padding:0upx 50upx;align-items: center;z-index: 31; border-bottom: 1upx solid #EFEFEF ;">
			<view class="icontext" style="width: 80upx;font-size: 23upx;" @click="tap_mulu('paixu')">排序&nbsp;&nbsp;&#xe602;</view>
			<picker class="icontext" style="width: 80upx;font-size: 23upx;" :range="filter_fanwei" @change="pickerChange">
				{{filter_picker_name}}&nbsp;&nbsp;&#xe602;
			</picker>
			<view class="icontext" style="width: 80upx;font-size: 23upx;" @click="tap_mulu('ziduan')">字段&nbsp;&nbsp;&#xe602;</view>
		</view>



		<scroll-view scroll-y class="jichukuangjia" :style="'height:'+height+'px'  " v-show="current === 0" @scroll="scroll">

			<view class="" style="width: 100%;height: 100upx;background-color: white;flex-flow: row; padding:0upx 15upx;justify-content:flex-start;align-items: center;">
				<image class="leixing_view" src="../../../static/leixing/line.png"></image>
				<image class="leixing_view" src="../../../static/leixing/pie.png"></image>
			</view>

			<view :class="['rank_title_view',fix_title==true ? 'fix_style' : '']" v-show="current === 0">
				<view class="rank_title" v-for="(item,index) in  rank['rank_title_show']" :key="index" :style="'width:'+width+'px'">
					{{rank['rank_title_show'][index]}}
				</view>
			</view>


			<view class="" v-for="(item1,index1) in  data_list" :key="index1" style="width: 750upx; height: 50upx; flex-flow: row nowrap;"
			 @click="tap_item(item1)">
				<view :class="['rank_title',index1%2==0 ? 'dark_bacgound' : '']" style="width: 40upx; font-size: 20upx; color: #0000FF;">{{index1+1}}</view>
				<view :class="['rank_title',index1%2==0 ? 'dark_bacgound' : '']" v-for="(item2,index2) in  rank['rank_title_show']"
				 :key="index2" :style="'width:'+width+'px'">
					{{data_list[index1][rank['rank_title_name_show'][index2]]}}
				</view>
			</view>
			<view class="" style="flex-flow: column;justify-content: center;align-content: center;align-items: center;">
				<view @click="gotonewitem" style="font-size: 24upx;">
					<view class="icontext" style="font-size: 74upx;color: #007AFF;">&#xe641;</view>
					<view class="icontext" style="font-size: 34upx;"> 创建 </view>
				</view>
			</view>
		</scroll-view>



		<scroll-view scroll-y class="jichukuangjia" :style="'height:'+height2+'px' " v-if="current === 1">
			<handleview :card_id="luyouData[list_id].card_id"></handleview>
			<!-- <handleview card_id="7" > </handleview> -->
		</scroll-view>



	</view>
</template>

<script>
	import navigationview from '../../../components/navigation.vue';

	import handleview from '../../../components/handle-view.vue';
	export default {
		data() {
			return {
				luyouData: [{}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {},
					{}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {},
				],
				list_id: null,
				items: [
					'排行',
					'改进',
				],
				minheight: 0,
				fix_title: false,
				height: 0,
				height2: 0,
				width: 0,
				current: 0,
				data_list: [],
				data_list_xiazai: [],
				filter_fanwei: null,
				filter_name: null,
				filter_picker_name: "筛选",
				rank: {

					'rank_title': [],
					'rank_title_name': [],
					'rank_title_show': [],
					'rank_title_name_show': [],
					'rank_title_type': [],
					'rank_title_type_show': [],
					'paixu': [],
					'ziduan': [],
				},
				mulu_flag: 'null',
				paixu_sort_flag: 'bigtosmall'
			}
		},
		components: {
			handleview,
			navigationview,
		},
		onLoad: async function(option) {
			console.log("list_id:" + option.list_id)
			this.list_id = option.list_id;
			let aa = await this.get_hot_card_list()
			this.rank['rank_title'] = this.luyouData[this.list_id].rank_title.split(',');
			this.rank['rank_title_name'] = this.luyouData[this.list_id].rank_title_name.split(',');
			this.rank['rank_title_type'] = this.luyouData[this.list_id].rank_title_type.split(',');
			for (let i = 0; i < 6; i++) {
				if (this.rank['rank_title'][i] != null) {
					this.rank['rank_title_show'][i] = this.rank['rank_title'][i];
					this.rank['rank_title_name_show'][i] = this.rank['rank_title_name'][i];
					this.rank['rank_title_type_show'][i] = this.rank['rank_title_type'][i];

				}
			}
			this.minheight = 150 * (uni.getSystemInfoSync().windowWidth / 750);
			this.height2 = uni.getSystemInfoSync().windowHeight - 70 * (uni.getSystemInfoSync().windowWidth / 750);
			this.height = uni.getSystemInfoSync().windowHeight - 120 * (uni.getSystemInfoSync().windowWidth / 750);
			this.width = (710 / this.rank['rank_title_show'].length) * (uni.getSystemInfoSync().windowWidth / 750);
			setTimeout(() => this.getData_list(), 500);
		},

		methods: {
			onClickItem(index) {
				if (this.current !== index) {
					this.current = index;
				}
			},
			tap_mulu(flag) {
				if (this.mulu_flag != 'null') {
					this.mulu_flag = 'null'
				} else {
					this.mulu_flag = flag
				}
			},
			tap_paixu_sort(flag) {
				this.paixu_sort_flag = flag;
				this.$forceUpdate();

			},
			tap_paixu(index) {
				for (let i = 0; i < this.rank['rank_title'].length; i++) {
					if (i == JSON.stringify(index)) {
						this.rank['paixu'][i] = true
					} else(
						this.rank['paixu'][i] = false
					)
				}
				this.$forceUpdate();
			},
			tap_ziduan(index) {

				if (this.rank['ziduan'][JSON.stringify(index)]) {
					this.rank['ziduan'][JSON.stringify(index)] = !this.rank['ziduan'][JSON.stringify(index)]
				} else {
					var true_num = 0;
					for (let i = 0; i < this.rank['rank_title'].length; i++) {
						if (this.rank['ziduan'][i]) {
							true_num += 1
						}
					}
					if (true_num < 6) {
						this.rank['ziduan'][JSON.stringify(index)] = true
					}
				}
				this.$forceUpdate();

			},

			tap_paixu_queren() {
				var paixu_name = '';
				var paixu_num = null;
				var that = this;
				for (let i = 0; i < that.rank['paixu'].length; i++) {
					if (that.rank['paixu'][i]) {
						paixu_name = that.rank['rank_title_name'][i];
						paixu_num = i;
						break;
					}
				}
				console.log(that.rank['rank_title_type'])
				that.data_list.sort(function(a, b) {
					var value1 = null,
						value2 = null;
					if (that.rank['rank_title_type'][paixu_num] == 'str' || that.rank['rank_title_type'][paixu_num] == 'filter') {
						value1 = a[paixu_name].toString();
						value2 = b[paixu_name].toString();
						if (that.paixu_sort_flag == 'bigtosmall') {
							return value1.localeCompare(value2);
						}
						if (that.paixu_sort_flag == 'smalltobig') {
							return value2.localeCompare(value1);
						}
					}
					if (that.rank['rank_title_type'][paixu_num] == 'num') {
						value1 = Number(a[paixu_name]);
						value2 = Number(b[paixu_name]);
						if (that.paixu_sort_flag == 'bigtosmall') {
							return value1 - value2;
						}
						if (that.paixu_sort_flag == 'smalltobig') {
							return value2 - value1;
						}
					}
					// console.log(value2,'-',value1,'-',(value1.localeCompare(value2)?1:0))	
					// return value2 - value1;
				});

				this.mulu_flag = 'null'
				this.$forceUpdate();

			},

			tap_ziduan_queren() {
				this.rank['rank_title_show'] = [];
				this.rank['rank_title_name_show'] = [];
				this.rank['rank_title_type_show'] = [];
				for (let i = 0; i < this.rank['rank_title'].length; i++) {
					if (this.rank['ziduan'][i]) {
						this.rank['rank_title_show'].push(this.rank['rank_title'][i])
						this.rank['rank_title_name_show'].push(this.rank['rank_title_name'][i])
						this.rank['rank_title_type_show'].push(this.rank['rank_title_type'][i])
					}
				}
				this.width = (710 / this.rank['rank_title_show'].length) * (uni.getSystemInfoSync().windowWidth / 750);
				this.mulu_flag = 'null'
				this.$forceUpdate();

			},

			scroll(e) {
				// console.log(e.detail.scrollTop)
				if (Number(e.detail.scrollTop) > this.minheight) {
					this.fix_title = true;
					// console.log(this.minheight)
					// console.log(this.fix_title)
				} else {
					this.fix_title = false;
					// console.log(this.minheight)
					// console.log(this.fix_title)
				}
			},



			pickerChange(e) {
				var that = this;
				// console.log(e.detail.value)
				if (that.filter_fanwei != null) {
					console.log(that.filter_fanwei[e.detail.value])
					uni.showLoading({
						title: '数据整理'
					})
					that.data_list = [];
					for (let i = 0; i < that.data_list_xiazai.length; i++) {
						if (that.data_list_xiazai[i][that.filter_name] == that.filter_fanwei[e.detail.value]) {
							that.data_list.push(that.data_list_xiazai[i])
						}
					}
					setTimeout(function() {
						uni.hideLoading();
						that.$forceUpdate();
						that.filter_picker_name = that.filter_fanwei[e.detail.value];
					}, 200);

				}



			},
			zhengli_filter() {
				var result = [];
				var filter_name = null;
				var filter_num = null;
				var that = this;
				for (let i = 0; i < that.rank['rank_title_type'].length; i++) {
					if (that.rank['rank_title_type'][i] == 'filter') {
						filter_name = that.rank['rank_title_name'][i];
						filter_num = i;
						break;
					}
				}
				// console.log(filter_name)
				that.filter_name = filter_name;
				if (filter_name != null) {
					for (let i = 0; i < that.data_list_xiazai.length; i++) {
						for (let j = 0; j < that.data_list_xiazai.length; j++) {
							if (that.data_list_xiazai[i][filter_name] == that.data_list_xiazai[j][filter_name]) {
								j = ++i;
							}
						}
						result.push(that.data_list_xiazai[i][filter_name]);
					}
					that.filter_fanwei = result;
					for (let i = 0; i < that.data_list_xiazai.length; i++) {
						if (that.data_list_xiazai[i][filter_name] == result[0]) {
							that.data_list[i] = that.data_list_xiazai[i]
						}
					}
				} else {
					that.data_list = that.data_list_xiazai
				}



			},






			gotonewitem: function() {
				uni.navigateTo({
					url: "../item_detail/item_detail?data={}&card_id=" + this.luyouData[this.list_id].card_id +
						"&new_item_flag=true",
				})
			},
			tap_item: function(item3) {
				uni.navigateTo({
					url: "../item_detail/item_detail?data=" + JSON.stringify(item3) + "&card_id=" + this.luyouData[this.list_id].card_id,
				})
			},

			getData_list() {
				var that = this;
				uni.request({
					url: 'https://kaikaiomg.applinzi.com/uni_app_download.php',
					method: "POST",
					data: {
						'form': this.luyouData[this.list_id].getdata_title,
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (ret) => {
						if (ret.statusCode !== 200) {
							console.log("请求失败:", ret)

						} else {
							console.log("下载数据:", ret.data)
							this.data_list_xiazai = ret.data.data;
							setTimeout(function() {
								that.zhengli_filter()
							}, 100)
						}
					}
				});
			},

		}
	}
</script>

<style>
	.rank_title {
		line-height: 50upx;
		align-content: center;
		justify-content: center;
		font-size: 20upx;
		overflow: hidden;
	}

	.rank_title_view {
		padding-left: 40upx;
		width: 710upx;
		height: 50upx;
		flex-flow: row nowrap;
		background-color: #EEEEEE;
		border-bottom: 1px solid #EEEEEE;
	}

	.fix_style {
		position: fixed;
		top: 120upx;
	}

	.leixing_view {
		flex-flow: column;
		width: 60upx;
		height: 60upx;
        /* border: 3px solid black ; */
        /* border-radius: 60upx; */
		margin: 0upx 12upx;
		background-color: ;
	}

	.dark_bacgound {
		background-color: #ADD8E6;
	}

	.jichukuangjia {
		display: flex;
		position: fixed;
		bottom: 0upx;
		/* 		background-image: url('http://kaikaiomg-kaikaiomg.stor.sinaapp.com/uni_app_image/bc.jpg'); */
		background-color: #F5F5F5;
		width: 750upx;
		overflow: hidden;
		flex-flow: column nowrap;
		bottom: 0upx;
		background-color: #EFEFEF;
		/* justify-content: center; */
		/* align-items: center; */
	}

	.rank_title_bac {
		padding: 15upx;
		/* border: 1upx solid blue; */
		border-radius: 7upx;
		background-color: #DCDCDC;
		font-size: 25upx;
		margin: 10upx;
		color: black;
	}

	.bac_blue_white {
		background-color: #1E90FF;
		color: white;
	}

	.bac_red_white {
		background-color: red;
		color: white;
	}

	@keyframes mymove {
		from {
			top: -420px;
		}

		to {
			top: 120px;
		}
	}
</style>



